import { Component, ViewEncapsulation, ContentChild, AfterContentInit } from '@angular/core';
import { AvlSideNavComponent } from '../avl-side-nav/avl-side-nav.component';
import { AvlAppBarComponent } from '../avl-app-bar/avl-app-bar.component';

@Component({
  selector: 'avl-app-layout',
  templateUrl: './avl-app-layout.component.html',
  styleUrls: [
    './avl-app-layout.component.css'
  ],
  encapsulation: ViewEncapsulation.Emulated
})
export class AvlAppLayoutComponent implements AfterContentInit {
    /**
   *
   * ### Styling
   * The following custom properties are available for styling:
   *
   * Custom property                          | Description                         | Default
   * :----------------------------------------|:------------------------------------|:-----------
   * `--avl-app-layout-content-padding`       | Padding around main slotted content | `24px`
   * `--avl-primary-text-color`               | Text color of main slotted content  | ``
   *
   */
  _styling: any;

  @ContentChild(AvlSideNavComponent) sideNav: AvlSideNavComponent;
  @ContentChild(AvlAppBarComponent) appBar: AvlAppBarComponent;

  constructor() {}

  ngAfterContentInit() {
    if(this.sideNav && this.appBar) {
      this.appBar.burgerClicked.subscribe( () => this.sideNav.open = !this.sideNav.open );
    }
  }
}
