import { Component, OnInit, ElementRef } from '@angular/core';

@Component({
  selector: 'avl-app-bar-item',
  template: '<ng-content></ng-content>',
  styleUrls: []
})
export class AvlAppBarItemComponent implements OnInit {

  constructor(private elementRef: ElementRef) { }

  /**
   * @hidden
   */
  ngOnInit() {
    (this.elementRef.nativeElement as HTMLElement).classList.add('mdc-toolbar__icon');
  }

}
