import {
  Component,
  Input,
  Output,
  ViewEncapsulation,
  EventEmitter,
  OnInit,
  HostBinding
} from '@angular/core';

import '@avl-controls/core/dist/avl-app-switcher/avl-app-switcher';
import '@avl-controls/core/dist/avl-icon/avl-icon.js';

export class MenuButtonItems {
  constructor(
    readonly headerText: string,
    readonly headerIconName: string,
    readonly items: Array<{ itemText: string; iconName: string }>,
    readonly footerItems?: Array<{ itemText: string; iconName: string }>
  ) {}
}

/**
 * Object representing sub-application that is added to app-bar drop-down
 */
export class ApplicationReference {
  name: string;
  icon?: string;
  image?: string;
  [key: string]: any;
}

@Component({
  selector: 'avl-app-bar',
  templateUrl: './avl-app-bar.component.html',
  styleUrls: ['./avl-app-bar.component.css'],
  encapsulation: ViewEncapsulation.Emulated
})
export class AvlAppBarComponent implements OnInit {
  /**
   *
   * ### Styling
   * The following custom properties are available for styling:
   *
   * Custom property                          | Description                                   | Default
   * :----------------------------------------|:----------------------------------------------|:-------------------------
   * `--avl-app-bar-background-color`         | Background color of app-bar                   | `--avl-primary-color`
   * `--avl-app-bar-height`                   | Height of app-bar (minus 3px bottom line)     | `53px`
   * `--avl-app-bar-item-color`               | Text color of app-bar items                   | `--avl-on-primary-color`
   * `--avl-app-bar-underline-color`          | Underline color                               | `--avl-product-color`
   */
  _styling: any;

  /**
   * Tells which is the currently selected application
   */
  @Input()
  public currentApplication: ApplicationReference;

  /**
   * Boolean to toggle responsive features
   * On mobile screens sidenav toggling is triggered by burger icon in app-bar as part of app-layout
   */
  @Input()
  @HostBinding('class.responsive')
  public responsive: boolean;

  /**
   * Boolean to set only mobile features on
   * On mobile screens sidenav toggling is triggered by burger icon in app-bar as part of app-layout
   */
  @Input()
  @HostBinding('class.mobile')
  public mobile: boolean;

  /**
   * URL to application home/ landing page
   */
  @Input()
  public homePageUrl: string;

  /**
   * URL to application icon / logo
   */
  @Input()
  public appLogo: string;

  /**
   * List of sub-applications used
   */
  @Input() applications: ApplicationReference[] = [];

  /**
   * Event emitted when new application is added.
   */

  @Output() applicationListChange: EventEmitter<ApplicationReference[]> = new EventEmitter<ApplicationReference[]>();

  /**
   * Flag indicating app-bar has fixed positioning or no
   */
  @Input() fixed: boolean = true;


  /**
   * Event emitted when application is changed in app-bar drop-down
   */
  @Output()
  currentApplicationChange: EventEmitter<ApplicationReference> = new EventEmitter<ApplicationReference>();

  /**
   * Event emmited when Burger menu is clicked.
   * Toggles side-nav visibility if app-bar is part of app-layout in responsive mode
   */
  @Output() burgerClicked: EventEmitter<void> = new EventEmitter<void>();

  /** @hidden */
  public appLogoSet: boolean = false;

  private appLogoPreset: boolean = false;

  /** @hidden */
  _appsLength: number;

  /**
   * True if only one element added to applications array
   */
  get isSingleApp(): boolean {
    return !this.applications || this._appsLength < 2;
  }

  /**
   * @hidden
   */
  get showAppLogo(): boolean {
    if (this.currentApplication == null) return false;
    const image = this.currentApplication.image;
    return !(image == null || image === '');
  }
  /**
   * @hidden
   */
  burgerIconClicked() {
    this.burgerClicked.emit();
  }

  /**
   * @hidden
   */
  onApplicationChanged(app: ApplicationReference) {
    this.currentApplication = app;
    if (!this.appLogoPreset) {
      setTimeout(() => {
        this.appLogoSet = true;
        this.appLogo = this.currentApplication.image;
      });
    }
    this.currentApplicationChange.emit(this.currentApplication);
  }

  /**
   * @hidden
   */
  onApplicationListChanged(apps: ApplicationReference[]) {
    this.applications = apps;
    this.applicationListChange.emit(apps);
  }

  /** @hidden */
  ngOnInit() {
    if (this.appLogo) {
      this.appLogoSet = true;
      this.appLogoPreset = true;
    } else {
      this.appLogo = this.currentApplication ? this.currentApplication.image : '';
      this.appLogoSet = true;
    }
    this._appsLength = this.applications ? this.applications.length : 0;
  }

}