import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';

// toolbar stuff
import { AvlAppBarComponent } from './components/avl-app-bar/avl-app-bar.component';
import { AvlAppBarItemComponent } from './components/avl-app-bar/avl-app-bar-item.component';

// sidenav stuff
import { AvlSideNavComponent } from './components/avl-side-nav/avl-side-nav.component';

import { AvlAppLayoutComponent } from './components/avl-app-layout/avl-app-layout.component';
import { AvlNoPaddingDirective } from './components/avl-app-layout/avl-no-padding.directive';

import { AvlLongPageComponent } from './components/avl-long-page/avl-long-page.component';
import { AvlParagraphDirective } from './components/avl-long-page/avl-paragraph.directive';

import { AvlSolutionOverviewComponent } from './components/avl-solution-overview/avl-solution-overview.component';
import { AvlNgBindersModule } from '@avl-controls/ng-binders';

import { AvlListExpanderComponent } from './components/avl-list-expander/avl-list-expander.component';

const componentsAndDirectives = [
  AvlAppBarComponent,
  AvlAppBarItemComponent,
  AvlSideNavComponent,
  AvlAppLayoutComponent,
  AvlNoPaddingDirective,
  AvlLongPageComponent,
  AvlParagraphDirective,
  AvlSolutionOverviewComponent,
  AvlListExpanderComponent,
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule,
    AvlNgBindersModule
  ],
  declarations: componentsAndDirectives,
  schemas: [
    CUSTOM_ELEMENTS_SCHEMA
  ],
  exports: componentsAndDirectives
})
export class AvlNavigationModule { }
