import {
  Component,
  HostListener,
  ElementRef,
  ViewChildren,
  QueryList,
  Input,
  EventEmitter,
  Output,
  ViewChild,
  AfterViewInit,
  OnInit,
  OnChanges,
  SimpleChanges,
  OnDestroy,
  ChangeDetectorRef,
} from '@angular/core';
import { BehaviorSubject, Observable, asyncScheduler, Subscription } from 'rxjs';
import { throttleTime, map } from 'rxjs/operators';
import { KeyboardNavigator } from './keyboard-navigator';

import '@avl-controls/core/dist/avl-icon/avl-icon';
import '@avl-controls/core/dist/avl-search-bar/avl-search-bar';

export interface AvlSolutionOverviewItem {
  /**
   * Label of the item.
   */
  label: string;

  /**
   * Router which to navigate to when the item is clicked.
   * Shouldn't be used together with link.
   */
  route?: string;

  /**
   * URL which to navigate to when the item is clicked.
   * Shouldn't be used together with route.
   */
  link?: string;

  /**
   * Specifies where to open the linked document (_blank | _self | _parent | _top | framename).
   * Used only when link is set.
   */
  target?: string;

  /**
   * Description of the item.
   */
  description?: string;

  /**
   * Icon of the item.
   * Only one of icon and iconUrl can be used. The other should be undefined.
   */
  icon?: string;

  /**
   * URL of the icon image of the item.
   * Only one of icon and iconUrl can be used. The other should be undefined.
   */
  iconUrl?: string;

  /**
   * Don't show circle around icon. Different color will be applied to icon if enabled
   * Circle is visible by default
   */
  noIconCircle?: boolean;
}

export interface AvlSolutionOverviewGroup {
  /**
   * If true, additional space for description text will be provided.
   * It is disabled by default
   */
  description?: boolean;

  /**
   * Title for the group
   */
  title?: string;

  /**
   * Text above group
   */
  header?: string;

  /*
   * Text below group
   */
  footer?: string;

  /**
   * Group items
   */
  items: AvlSolutionOverviewItem[];
}

@Component({
  selector: 'avl-solution-overview',
  templateUrl: './avl-solution-overview.component.html',
  styleUrls: ['./avl-solution-overview.component.css'],
})
export class AvlSolutionOverviewComponent implements AfterViewInit, OnInit, OnChanges, OnDestroy {

  constructor(private cdr: ChangeDetectorRef) {}

  /**
   *
   * ### Styling
   * The following custom properties are available for styling:
   *
   * Custom property                                   | Description                        | Default
   * :-------------------------------------------------|:-----------------------------------|:----------------------
   * `--avl-solution-overview-background-color`        | Background color of the overview   | `--avl-background-1-color`
   * `--avl-solution-overview-text-color`              | Text color                         | `--avl-foreground-color`
   * `--avl-solution-overview-item-background-color`   | Item background color              | `--avl-container-background-color`
   * `--avl-solution-overview-background-image`        | Background image of the overview   |  auto
   * `--avl-solution-overview-item-hover-color`        | Item background color when hovered | `--avl-container-background-product-hover-color`
   * `--avl-solution-overview-item-focus-color`        | Item background color when focused | `--avl-container-background-product-focus-color`
   * `--avl-solution-overview-item-pressed-color`      | Item background color when pressed | `--avl-container-background-product-pressed-color`
   * `--avl-solution-overview-title-color`             | Title color                        | `--avl-foreground-color``
   * `--avl-solution-overview-subtitle-color`          | Subtitle color                     | `--avl-foreground-medium-color``
   * `--avl-solution-overview-item-icon-color`         | Item icon color                    | `--avl-on-primary-color`
   * `--avl-solution-overview-item-icon-color-no-circle` | Item icon color if noIconCircle is set | `--avl-hint-color`
   * `--avl-solution-overview-item-icon-circle-color`  | Color for icon circle              | `--avl-product-color`
   * `--avl-solution-overview-item-icon-pressed-color` | Item icon color when pressed       | `--avl-solution-overview-item-icon-color`
   * `--avl-solution-overview-item-description-color`  | Item description text color        | `--avl-foreground-medium-color`
   */
  _styling: any;

  /**
   * Groups defining sections with buttons
   */
  @Input()
  groups: AvlSolutionOverviewGroup[];

  /**
   * Solution overview title.
   */
  @Input()
  public solutionName: string;

  /**
   * URL of the icon image of the solution overview title.
   */
  @Input()
  public solutionIconUrl: string;

  /**
   * Solution overview subtitle.
   */
  @Input()
  public solutionSubtitle?: string;

  /**
   * Flag for the optional search bar.
   * If true, search bar is shown.
   */
  @Input()
  public showSearchBar: boolean;

  /**
   * Emits search query string on change
   */
  @Output()
  public searchQueryChange = new EventEmitter<string>();

  /**
   * Emits value of the search query when search triggered
   */
  @Output()
  public search = new EventEmitter<string>();

  gridWidthClass$: Observable<string>;

  /** @hidden */
  @ViewChildren('itemElement') buttonRefs: QueryList<ElementRef>;

  /** @hidden */
  @ViewChildren('group') groupElementRefs: QueryList<ElementRef>;

  /** @hidden */
  @ViewChild('searchbar') searchbarRef: ElementRef;

  /** @hidden */
  @ViewChild('iconButtons') iconButtonsContainer: ElementRef;

  /** @hidden */
  @ViewChild('searchbarContainer') searchbarContainer: ElementRef;

  /** @hidden */
  @ViewChild('groupsContainer') groupsContainer: ElementRef;

  /** @hidden */
  contentHasScroll: boolean;

  private gridWidth$ = new BehaviorSubject<number>(-1);

  private lastFocusedEl: HTMLElement;

  private maxColumns: number;

  private largestItemsIndex = -1;
  private maxItems = 0;

  // cache screenWidth for maxColumns calculation
  private screenWidth: number;

  // cache icon buttons since they can hardly be changed dynamically
  private iconButtonElementsCache?: HTMLElement[];

  private groupElementRefsChangesSubscription: Subscription;

  private get groupsOfItems(): HTMLElement[][] {
    let v = [];
    for (const gRef of this.groupElementRefs) {
      const gEl = gRef.nativeElement;
      const items = Array.prototype.slice.call(gEl.querySelectorAll('a.item'));

      v = [...v, items];
    }
    return v;
  }

  private get iconButtonElements(): HTMLElement[] {
    if (this.iconButtonElementsCache == null) {
      this.iconButtonElementsCache = Array.prototype.slice.call(
        (this.iconButtonsContainer
          .nativeElement as HTMLElement).querySelectorAll('avl-icon-button'));
    }
    return this.iconButtonElementsCache;
  }

  ngOnInit() {
    this.setupReferenceVars();
    this.gridWidthClass$ = this.gridWidth$.asObservable().pipe(
      throttleTime(50, asyncScheduler, {trailing: true}),
      map(gridWidth => {
        return `grid-width-${gridWidth}`;
      })
    );
  }

  ngOnDestroy() {
    if (this.groupElementRefsChangesSubscription) {
      this.groupElementRefsChangesSubscription.unsubscribe();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.groups && !changes.groups.isFirstChange()) {
      this.setupReferenceVars();
      this.gridWidth$.next(this.getGridWidth());
      this.setTopMargin();
    }
  }

  /** @hidden */
  ngAfterViewInit() {
    this.setTopMargin();
    this.setupFocusOrdering();
    this.gridWidth$.next(this.getGridWidth());
  }

  /** @hidden */
  getMaxColumnsClass(): string {
    if (this.maxColumns && window.outerWidth === this.screenWidth) {
      return `max-columns-${this.maxColumns}`;
    }
    if (this.maxItems === 0) {
      return;
    }
    let maxColumns: number;
    switch (this.maxItems) {
      case 1:
        maxColumns = 1;
        break;
      case 2:
      case 4:
        maxColumns = 2;
        break;
      case 3:
      case 5:
      case 6:
      case 9:
        maxColumns = 3;
        break;
      case 7:
      case 8:
      case 11:
      case 12:
        maxColumns = 4;
        break;
      default:
        maxColumns = 5;
    }
    this.maxColumns = maxColumns;
    return `max-columns-${maxColumns}`;
  }

  /** @hidden **/
  @HostListener('window:resize')
  onResize() {
    this.gridWidth$.next(this.getGridWidth());
  }

  /** @hidden */
  @HostListener('keydown', ['$event'])
  onKeydownHandler(event: KeyboardEvent) {
    const gOfItems = this.groupsOfItems;
    const numGroups = gOfItems.length;
    if (numGroups === 0) {
      return;
    }

    const activeEl = document.activeElement as HTMLElement;
    const isDown = ['Down', 'ArrowDown'].includes(event.key);
    const isUp = ['Up', 'ArrowUp'].includes(event.key);
    const isRight = ['Right', 'ArrowRight'].includes(event.key);
    const isLeft = ['Left', 'ArrowLeft'].includes(event.key);
    const isTab = event.key === 'Tab';

    const searchbar = this.showSearchBar
      ? (this.searchbarRef.nativeElement as HTMLElement)
      : undefined;

    let gNum: number;
    let iIdx: number;

    for (const items of gOfItems) {
      if (items.indexOf(activeEl) !== -1) {
        iIdx = items.indexOf(activeEl);
        gNum = gOfItems.indexOf(items);
        // Go to route on 'Space'. 'Enter' already works on <a>.
        if (event.key === ' ') {
          items[iIdx].click();
          return;
        }
      }
    }

    if (this.iconButtonElements.includes(activeEl)) {
      const numIconButtons = this.iconButtonElements.length;
      const idx = this.iconButtonElements.indexOf(activeEl);
      if ((isRight || isTab) && idx === numIconButtons - 1) {
        if (searchbar != null) {
          searchbar.focus();
        } else {
          gOfItems[0][0].focus();
        }
      } else if (isLeft && idx === 0) {
        const g = gOfItems[numGroups - 1];
        const gLen = g.length;
        g[gLen - 1].focus();
      } else if (isLeft) {
        this.iconButtonElements[idx - 1].focus();
      } else if (isRight) {
        this.iconButtonElements[idx + 1].focus();
      }
      event.preventDefault();
    }

    const itemElements = gOfItems[gNum];
    const gridWidth = this.gridWidth$.value;

    if (searchbar != null && iIdx < gridWidth && gNum === 0 && isUp) {
      searchbar.focus();
      event.preventDefault();
    } else if (
      (isRight || isTab) &&
      gNum === gOfItems.length - 1 &&
      iIdx === itemElements.length - 1
    ) {
      const iconButtons = this.iconButtonElements;
      if (iconButtons != null && iconButtons.length > 0) {
        iconButtons[0].focus();
        event.preventDefault();
      }
    } else if (searchbar != null && searchbar === activeEl && isDown) {
      gOfItems[0][0].focus();
      event.preventDefault();
    } else if (gNum != null) {
      const nextGroup = gNum === gOfItems.length - 1 ? undefined : gOfItems[gNum + 1];
      const prevGroup = gNum === 0 ? undefined : gOfItems[gNum - 1];

      const nGLen = nextGroup == null ? 0 : nextGroup.length;
      const pGLen = prevGroup == null ? 0 : prevGroup.length;

      const keyboardNavigator = new KeyboardNavigator(
        itemElements.length,
        gridWidth
      );
      const itemIndexToMoveTo = keyboardNavigator.navigate(
        iIdx,
        event.key,
        nGLen,
        pGLen
      );
      if (itemIndexToMoveTo != null) {
        if ((isUp || isLeft) && itemIndexToMoveTo < 0) {
          prevGroup[itemIndexToMoveTo + pGLen].focus();
        } else if ((isDown || isRight) && iIdx + 1 > itemIndexToMoveTo) {
          nextGroup[itemIndexToMoveTo].focus();
        } else {
          gOfItems[gNum][itemIndexToMoveTo].focus();
        }
        event.preventDefault();
      }
    }
  }

  /** @hidden */
  onQueryChange(event: any) {
    this.searchQueryChange.emit(event.detail.value);
  }

  /** @hidden */
  onSearch(event: any) {
    this.search.emit(event.detail.value);
  }

  private setupReferenceVars(): void {
    let max = 0;
    for (const g of this.groups) {
      const itemSz = g.items.length;
      if (max < itemSz) {
        max = itemSz;
        this.largestItemsIndex = this.groups.indexOf(g);
      }
    }
    this.maxItems = max;
  }

  private setTopMargin() {
    this.contentHasScroll = false;
    this.cdr.detectChanges();
    const content = this.groupsContainer.nativeElement;
    this.contentHasScroll = content.scrollHeight > content.clientHeight;
    this.cdr.detectChanges();
  }

  private getGridWidth(): number {
    if (this.largestItemsIndex < 0) {
      this.setupReferenceVars();
    }
    const items = this.groupsOfItems[this.largestItemsIndex];
    const firstItemOffsetTop = items[0].offsetTop;

    let i = 0;
    for (const item of items) {
      if (item.offsetTop > firstItemOffsetTop) {
        return i;
      }
      i++;
    }
    return items.length;
  }

  private setupFocusOrdering() {
    const buttonElements: HTMLElement[] = this.buttonRefs.map(elRef => elRef.nativeElement);
    let focusableElements: HTMLElement[] = [];

    if (this.showSearchBar) {
      focusableElements.push(this.searchbarRef.nativeElement);
    }
    focusableElements = [
      ...focusableElements,
      ...buttonElements,
      ...this.iconButtonElements
    ];

    focusableElements.forEach((el) => {
      el.addEventListener('focus', (event: FocusEvent) => {
        const t = event.target as HTMLElement;
        if (t.tagName.toLowerCase() === 'avl-icon-button') {
          if (this.showSearchBar && (
            ['body', 'html'].includes(
              document.activeElement.tagName.toLowerCase()
            ) ||
              this.lastFocusedEl == null ||
              (this.lastFocusedEl.tagName.toLowerCase() !== 'avl-icon-button' &&
               this.lastFocusedEl !== buttonElements[buttonElements.length - 1])
          )) {
            this.searchbarRef.nativeElement.focus();
            event.preventDefault();
          }
        }
        this.lastFocusedEl = event.target as HTMLElement;
      });
    });
  }
}
