import {
  Component, Output, EventEmitter, Input, OnDestroy, OnChanges, SimpleChanges, HostBinding
} from '@angular/core';
import { trigger, style, animate, transition} from '@angular/animations';
import { TopLevelPage, Group, Page, Params } from './menu-items';

import { Router, ActivationEnd, ActivatedRoute } from '@angular/router';
import { filter } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import '@avl-controls/core/dist/avl-icon/avl-icon';

const rowHeightPx = 48;

@Component({
  selector: 'avl-side-nav',
  templateUrl: './avl-side-nav.component.html',
  styleUrls: ['./avl-side-nav.component.css'],
  animations: [
    trigger('expandCollapse', [
      transition(':enter', [
        style({ 'max-height': '0px' }),
        animate('225ms ease-in', style({ transform: 'translateY(0%)', 'max-height': '200px' }))
      ]),
      transition(':leave', [
        style({ 'max-height': '200px' }),
        animate('180ms ease-in', style({ 'max-height': '0px' }))
      ])
    ])
  ]
})
export class AvlSideNavComponent implements OnDestroy, OnChanges {

  /**
   *
   * ### Styling
   * The following custom properties are available for styling:
   *
   * Custom property                          | Description                                                                                           | Default
   * :----------------------------------------|:------------------------------------------------------------------------------------------------------|:---------
   * `--avl-app-bar-height`                   | Height of the toolbar. Needed so that the avl-side-nav can be correctly positioned below the toolbar. | `56px`
   * `--avl-side-nav-product-color`           | Used for selected color                                                                               | `--avl-product-selected`
   * `--avl-side-nav-product-color-hover`     | Used for hover color.                                                                                 | `--avl-product-hover`
   * `--avl-side-nav-text-color`              | Color of the text                                                                                     | `--avl-emphasis-high-color`
   * `--avl-side-nav-icons-color`             | Color of icons                                                                                        | `--avl-emphasis-medium-color`
   * `--avl-side-nav-background-color`        | Background color of the avl-side-nav                                                                  | `--avl-side-nav-color`
   * `--avl-side-nav-open-width`              | Width of the avl-side-nav in the open state.                                                          | `256px`
   *
   */
  _styling: any;

  private openValue = false;

  /**
   * Can be set to open or close the side nav.
   * Returns the current state of the side nav.
   */
  @Input()
  @HostBinding('class.open')
  public get open(): boolean {
    return this.openValue;
  }

  /**
   * Items that are shown in the side nav.
   */
  @Input()
  public menuItems: Array<TopLevelPage | Group>;

  /**
   * Boolean to toggle responsive features
   * On mobile screens sidenav toggling is triggered by burger icon in app-bar as part of app-layout
   */
  @Input()
  @HostBinding('class.responsive')
  public responsive: boolean;

  /**
   * Boolean to set only mobile features on
   * On mobile screens sidenav toggling is triggered by burger icon in app-bar as part of app-layout
   */
  @Input()
  @HostBinding('class.mobile')
  public mobile: boolean;

  /**
   * Emitted when the open property changes.
   */
  @Output()
  public openChange: EventEmitter<boolean> = new EventEmitter();

  public set open(val: boolean) {
    this.openValue = val;
    this.openChange.next(val);
  }

  private subscription: Subscription;

  /**
   * @hidden
   */
  public selectedGroup: Group;

  /**
   * @hidden
   */
  public selectedPage: Page;

  /**
   * @hidden
   */
  public selectedParagraph: string;
  private selectedParagraphIndex: number;
  private params: any;

  constructor(
    private router: Router,

    public activeRoute: ActivatedRoute
    ) {
    activeRoute.queryParams.subscribe(p => {
      this.params = p;
    });
    this.subscription = this.router.events
      .pipe(filter(event => event instanceof ActivationEnd))
      .subscribe((event: ActivationEnd) => {
        this.selectItems();
      });
  }

  /**
   * @hidden
   */
  public toggleOpen() {
    if(!this.manuallyOpened) {
      this.open = !this.open;
    }
  }

  private manuallyOpened: boolean;

  /**
   * @hidden
   */
  public arrowToggleOpen() {
      this.manuallyOpened = !this.open;
      this.open = !this.open;
  }

  /**
   * @hidden
   */
  public mobileClose() {
    if(Math.max(document.documentElement.clientWidth, window.innerWidth) < 960) {
      this.open = false;
    }
  }

  /**
   * @hidden
   */
  public groupClicked(item) {
    if (!this.open) {
      this.open = true;
      if (!item.expanded) {
        item.expanded = true;
      }
    } else {
      item.expanded = !item.expanded;
    }
  }

  /**
   * @hidden
   */
  public getScrubberStyle() {
    return {
      height: this.selectedParagraph ? '14px' : '0px',
      top: `${this.selectedParagraphIndex * rowHeightPx}px`
    };
  }

  /**
  * @hidden
  */
  public trimSlash(str: string) {
    if (str[0] === '/') {
      str = str.substring(1, str.length);
    }
    if (str[str.length - 1] === '/') {
      str = str.substring(0, str.length - 1);
    }
    return str;
  }

  private selectItems() {
    if (!this.menuItems) {
      return;
    }
    const fragment = this.router.parseUrl(this.router.url).fragment;
    const url = this.router.url.split('#')[0];
    const queue: Array<Page | Group> = this.menuItems.slice();
    let currentGroup: Group;
    this.selectedGroup = undefined;
    this.selectedPage = undefined;
    this.selectedParagraph = undefined;
    let fallbackSelectedPage: any;
    while (queue.length) {
      const item = queue.pop();
      if (isGroup(item)) {
        currentGroup = item;
        item.subItems.map(subitem => { queue.push(subitem); });
      } else {
        if (fallbackSelectedPage == null && this.trimSlash(url).startsWith(this.trimSlash(item.route))) {
          fallbackSelectedPage = item;
        }
        if (item.route && this.trimSlash(url).endsWith(this.trimSlash(item.route + this.getUrlParams(item.params)))) {
          if (this.selectedPage && this.selectedPage.route && this.selectedPage.route.length > item.route.length) {
            continue;
          }
          if (item.params && item.params.length > 0) {
            const object = new Object();
            item.params.forEach(i => {
              object[i.name] = i.value;
            });
            if (JSON.stringify(this.params) === JSON.stringify(object)) {
              this.selectedPage = item;
            }
          } else {
            this.selectedPage = item;
          }
          if (isTopLevelPage(item)) {
            this.selectedGroup = undefined;
          } else {
            this.selectedGroup = currentGroup;
          }

          if (item.paragraphs && item.paragraphs.length > 0) {
            const paragraphIndex = item.paragraphs.findIndex((p) => p.id === fragment);
            if (fragment && item.paragraphs && paragraphIndex !== -1) {
              this.selectedParagraph = fragment;
              this.selectedParagraphIndex = paragraphIndex;
            }
          }
        }
      }
    }
    if (this.selectedPage == null) {
      this.selectedPage = fallbackSelectedPage;
    }
  }

  /**
  * @hidden
  */
  ngOnChanges(changes: SimpleChanges) {
    if (changes['menuItems']) {
      this.selectItems();
    }
  }

  /**
  * @hidden
  */
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  /**
  * @hidden
  */
  getParams(params: Params[]) {
    if (params && params.length > 0) {
      const object = {};
      params.forEach(param => {
        object[param.name] = param.value;
      });
      return object;
    } else {
      return null;
    }
  }

  /**
  * @hidden
  */
 getUrlParams(params: Params[]) {
  if (params && params.length > 0) {
    let url = '?';
    params.forEach(param => {
      url += param.name + '=' + param.value + '&';
    });
    return url.slice(0, -1);
  } else {
    return '';
  }
}
}

/**
* @hidden
*/
function isGroup(item: Page | Group): item is Group {
  return (item as Group).subItems !== undefined && (item as Group).subItems.length > 0;
}

/**
* @hidden
*/
function isTopLevelPage(item: Page | Group): item is TopLevelPage {
  return !isGroup(item) && ((item as TopLevelPage).icon !== undefined || (item as TopLevelPage).iconUrl !== undefined);
}

